body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App>.btn{
  border:1px solid red;
  background:red;
  color:white;
  padding:3px 10px;
  box-shadow:0 0 3px rgba(0,0,0,0.2);
  margin:4px;
  border-radius:4px;
}

.button2{
  box-sizing:border-box;
  height:32px;
  background:#ffffff;
  border:1px solid rgba(153,153,153,1);
  border-radius:4px;
  padding:4px 1em;
  margin:10px;
  position:relative;
  overflow:hidden;
}
.button2:focus{
  outline: none;
  border-color:black;
}
.button2>.circle{
  border-radius:50%;
  height:10px;
  width:10px;
  background:red;
  opacity:0.5;
  display:inline-block;
  -webkit-animation:1s big forwards;
          animation:1s big forwards;
  position:absolute;
  z-index:0;
}
.button2>.value{
  position:relative;
  z-index:1;
}
@-webkit-keyframes big{
  0%{
    -webkit-transform:scale(0.1);
            transform:scale(0.1)
  }
  100%{
    -webkit-transform:scale(10);
            transform:scale(10)
  }
}
@keyframes big{
  0%{
    -webkit-transform:scale(0.1);
            transform:scale(0.1)
  }
  100%{
    -webkit-transform:scale(10);
            transform:scale(10)
  }
}
